/* Allgemeine Stile */
.App {
  display: flex;
  flex-direction: column;
  align-items: center;
}

/* PlusButton Stile */
.PlusButton {
  all: unset;
  display: flex;
  background-color: black;
  padding: 0.5rem 1rem;
  max-width: 300px;
  width: 60%;
  height: 30px;
  border-radius: 10px;
  justify-content: center;
  align-items: center;
  color: white;
  font-size: medium;
  transition: background-color 0.3s ease;
}

.PlusButton:hover {
  background-color: #333;
}

.DeleteButton {
  all: unset;
  height: 32px;
  width: 32px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  transition: background-color 0.3s ease;
}

.DeleteButton:hover {
  background-color: #555;
}

.TodoItem {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0.5rem 1rem;
  margin: 0.5rem 0;
  background-color: #f1f1f1;
  border-radius: 10px;
}

.TodoButtonDiv {
  display: flex;
  flex-direction: row;
}

.TodoTextDiv {
  display: flex;
  flex-direction: column;
}

.TodoText {
  all: unset;
  font-weight: 600;
}

.TodoTextArea {
  all: unset;
  font-size: smaller;
  resize: none;
  width: 100%;
  height: 3rem;
  overflow: hidden;
  color: "black";
}

.Header {
  font-size: xx-large;
  font-weight: 700;
  margin-bottom: 1rem;
}

.TodoDone {
  text-decoration: line-through;
  color: gray;
}

.TodoDoneArea {
  color: rgb(170, 170, 170);
}